body {
	margin: 0;
	padding: 0;

	background-color: var(--color-bgc);
    font-family: 'Open Sans', sans-serif;

	transition: 300ms;
}

body::-webkit-scrollbar {
    display: none;
}

.core {
    width: 100%;
    min-height: 100%;

    transition: 300ms;
}

@media (max-width: 360px) {
    html {
        font-size: 10px;
    }
}
@media (min-width: 361px) and (max-width: 399px) {
    html {
        font-size: 11px;
    }
}
@media (min-width: 400px) and (max-width: 439px) {
    html {
        font-size: 12px;
    }
}
@media (min-width: 440px) and (max-width: 479px) {
    html {
        font-size: 13px;
    }
}
@media (min-width: 480px) and (max-width: 519px) {
    html {
        font-size: 14px;
    }
}
@media (min-width: 520px) and (max-width: 559px) {
    html {
        font-size: 15px;
    }
}
@media (min-width: 560px) and (max-width: 599px) {
    html {
        font-size: 16px;
    }
}
@media (min-width: 600px) and (max-width: 639px) {
    html {
        font-size: 17px;
    }
}
@media (min-width: 640px) and (max-width: 679px) {
    html {
        font-size: 18px;
    }
}
@media (min-width: 680px) and (max-width: 719px) {
    html {
        font-size: 19px;
    }
}
@media (min-width: 720px) and (max-width: 759px) {
    html {
        font-size: 20px;
    }
}
@media (min-width: 760px) and (max-width: 799px) {
    html {
        font-size: 21px;
    }
}
@media (min-width: 800px) and (max-width: 839px) {
    html {
        font-size: 22px;
    }
}
@media (min-width: 840px) and (max-width: 879px) {
    html {
        font-size: 23px;
    }
}
@media (min-width: 880px) {
    html {
        font-size: 24px;
    }
}

@media (min-height: 801px) {
    .core {
        height: 100%;
    }
}

@media (max-height: 800px) {
    .core {
        height: auto;
    }
}

@keyframes rubberBand {
	0% {
		transform: scale3d(1, 1, 0.1);
	}

	30% {
		transform: scale3d(1.25, 0.75, 0.1);
	}

	40% {
		transform: scale3d(0.75, 1.25, 0.1);
	}

	50% {
		transform: scale3d(1.15, 0.85, 0.1);
	}

	65% {
		transform: scale3d(.95, 1.05, 0.1);
	}

	75% {
		transform: scale3d(1.05, .95, 0.1);
	}

	100% {
		transform: scale3d(1, 1, 0.1);
	}
}

@keyframes slide_effect {
	0% {
		transform: translateX(-0.3rem);
	}

	100% {
		transform: translateX(0.1rem);
	}
}