@media (min-width: 1001px) {
	.contact_container {
		flex-direction: row;

		padding: 0;
		width: 100%;
		height: 100%;
	}

	.contact_container>.text_zone {
		padding: 80px 0 80px 30px;
		width: calc(100% - 30px);
		height: calc(100% - 160px);
		max-width: 640px;
	}

	.contact_container>.location_zone {
		width: 100%;	
		height: 100%;
		min-height: 100vh;
	}
}

@media (max-width: 1000px) {
	.contact_container {
		flex-direction: column;

		padding: 80px 30px;
		width: calc(100% - 60px);
		height: calc(100% - 160px);
	}

	.contact_container>.text_zone {
		padding: 0;
		width: 100%;
		height: 100%;
	}

	.contact_container>.location_zone {
		margin: 0 12px;
		width: calc(100% - 24px);
		height: 80%;
		min-height: 300px;
	}
}

.contact_container {
	display: flex;
	justify-content: center;
}

.contact_container>.text_zone {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.contact_container>.text_zone>h2 {
	margin: 20px 0;
	padding: 0;

	font-size: 2.6rem;
	font-weight: 600;
	letter-spacing: -2px;

	transition: 300ms;
}

.contact_container>.text_zone>h2>span {
	background: -webkit-linear-gradient(#E7527C, #F8E8DC);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;

	animation-duration: 800ms;
	animation-fill-mode: both;
	animation-iteration-count: 1;
	display: inline-block;
}

.contact_container>.text_zone>h2>span:hover {
	animation-name: rubberBand;

	background: -webkit-linear-gradient(#F8E8DC, #E7527C);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.contact_container>.text_zone>span {
	margin: 12px 0;
	padding: 0 12px;

	color: #FFFFFF;
	font-size: 15.6px;
	font-weight: 300;
	letter-spacing: 0.4px;
	line-height: 23px;
}

.contact_container>.location_zone {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	background-image: url('../../assets/images/location.png');
}

.contact_container>.location_zone>.location_info {
	margin: 0.8rem;
	padding: 0.8rem;
	width: 7.4rem;
	height: 3.4rem;

	color: #FFFFFF;
	background-color: #00000095;

	font-size: 0.7rem;
	font-weight: 300;
	line-height: 1rem;

	transition: 300ms;
}

.contact_container>.location_zone>.location_info>.email {
	font-size: 0.6rem;
	line-height: 1.8rem;
}